import { NativeModules } from 'react-native';

/**
 * 获取当前app的版本
 * @returns 当前app的版本
 */
export function getCurrentAppVersion() {
    const deviceInfo = NativeModules?.KSRCTBridge?.deviceInfo || {};
    const data = deviceInfo.data || {};
    const { appVersion } = data || {};

    return appVersion;
}
